body {
  position: static;
  background: #2d2928;
  margin: 0;
  font-family: 'Raleway', helvetica, arial, sans-serif;
  font-size: 16px;
  font-weight: 200;
  color: #ebebeb;
}
* {
  box-sizing: border-box;
}
p {
  margin: 0;
  font-size: 1.1rem;
  line-height: 1.5rem;
}
a {
  color: #ffd833;
  transition: all 0.1s ease-in !important;
  text-decoration: none;
  border-bottom: 0.05rem solid transparent;
}
a:hover,
a:focus {
  color: #ffe05c;
  text-decoration: none;
  border-bottom-color: #ffe05c;
}
a:active {
  color: #e5bf1f;
  border-bottom-color: #ffe05c;
}
h1 {
  margin: 0 0 0.75rem 0;
  font-size: 1.75rem;
  line-height: 2rem;
  font-weight: 200;
  color: #FFFFFF;
}
